import React, { useEffect, useState } from 'react'
import Button from '../../../../components/ui/Button'
import { useDispatch, useSelector } from 'react-redux'
import { logoutAction } from '../../actions/authAction'
import logoutIcon from "./logoutIcon.svg"
import { useNavigate } from 'react-router-dom'
import Loader from '../../../../components/loader/Loader'
import { getCurrentUser } from '../../../../libs/getCurrentUser'
import { getLogoutStatus } from '../../reducers/authSlice'
import ConfirmationModal from '../../../../components/confirmationModal/ConfirmationModal'

const Logout = () => {
  const [showConfirmationModal, setshowConfirmationModal] = useState(false)
    const logoutStatus = useSelector(getLogoutStatus)
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const currentUser = getCurrentUser();

    const logUserOut = async () => {
      try{
        const payload = {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${currentUser.userToken}
            `,
          },
        };
        dispatch(logoutAction(payload))
      }
      catch(error){
        console.log(error)
      }
    }

    useEffect(() => {
      if(logoutStatus === "fulfilled" && !currentUser.userName){
        navigate("/login")
      }
    }, [logoutStatus])

    if(logoutStatus === "loading"){
      return <Loader text={"Logging out"}/>
    }
  return (
    <div>
      {showConfirmationModal && <ConfirmationModal onConfirm={logUserOut} onCancel={() => setshowConfirmationModal(false)} heading={"Do you want to logout?"} />}
      <Button
        handleEvent={() => setshowConfirmationModal(true)}
        keyDownEvent={logUserOut}
        ariaLabel={`Logout ${currentUser.userName}`}
        text={<img src={logoutIcon} alt="Logout icon" />
      }
      />
    </div>
  )
}

export default Logout
