import React, { useEffect, useState } from 'react'
import "./profile-card.css"
import Button from '../../../../components/ui/Button'
import profileAvatar from "../../assets/svgs/profileAvatar.svg"
import { useLocation, useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { getCurrentUser } from '../../../../libs/getCurrentUser'
import { updateBio } from '../../../../services/bio/actions/updateBio'
import { muteUser } from '../../../../services/user/actions/muteUser'
import PageHeader from '../../../../components/pageHeader/PageHeader'
import { useWindowWidth } from '../../../landing'
import { getUserBio } from '../../../../services/bio/actions/getUserBio'
import { selectBio, selectBioById } from '../../../../services/bio/reducers/bioSlice'
import UpdateBioDetails from './components/updateBioDetails/UpdateBioDetails'
import { selectAllUser } from '../../../../services/user/reducers/userSlice'
import { getAllUserBios } from '../../../../services/bio/actions/getAllUserBios'
import { API_URL } from '../../../authentication'

const ProfileCard = () => {
    const [editBio, setEditBio] = useState(false)
    const {username} = useParams()
    const allUser = useSelector(selectAllUser)
    const bio = useSelector(selectBio)

    const dispatch = useDispatch()
    const currentUser = getCurrentUser()
    const {screenWidth} = useWindowWidth()
    const isMobile = screenWidth <= 767

    const user = allUser?.find(user => user?.username === username)
    const userBio =  bio?.find(bio => bio?.user._id === user._id)
    const sameUser = username === currentUser.userName

    const getCurrentUserBio = () => {
        try{
            const payload = {
                options: {
                    method: "GET",
                    headers:{
                        "Content-Type": "application/json",
                        Authorization : `Bearer ${currentUser.userToken}`
                    }
                },
                userUid: user._id
            }
            dispatch(getUserBio(payload))
        }
        catch(error){
            console.log(error)
        }
    }

    console.log(userBio)
    // useEffect(() => {
    //         getCurrentUserBio()
    // }, [username, user?._id])

    const editProfile = () => {
        try{
            const payload = {
                options: {
                    method: "PATCH",
                    headers:{
                        "Content-Type": "application/json",
                        Authorization : `Bearer ${currentUser.userTokem}`
                    }
                },
                userUid: user._id
            }

            dispatch(updateBio(payload))
        }
        catch(error){
            // console.log(error)
        }
        finally{
        }
    }

    const mute = () => {
        const options = {
            method: "PATCH",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${currentUser.userToken}
              `,
            },
        };
        dispatch(muteUser({options, userUid: user._id}))
    }

    // if(!user){
    //     return <h1>No User</h1>
    // }

  return (
    <section className="profile-card-section">
        <article className="profile-card" typeof="schema:Profile">
            {editBio && <UpdateBioDetails userBio={userBio} setEditBio={setEditBio} />}

            {/* <div className="profile-card-wrapper"> */}
           { isMobile && <PageHeader />}
                <div className="profile-card-details">
                    <div className="profile-details">
                        <div className="profile-avatar-container">
                            <img src={userBio?.avatar} className="profile-avatar" alt={`${user?.username} profile avatar`}/>
                        </div>

                        <div className="profile-metadata-container">
                            <h3 className="profile-name">{user?.username}</h3>
                            <small className="profile-creation-date">Joined March 2021</small>
                        </div>
                    </div>
                    
                    <div className="">
                        { 
                            sameUser
                            ? 
                                <Button 
                                    className={"profile-btn"} 
                                    text={"Edit profile"} 
                                    handleEvent={() => setEditBio(true)}
                                /> 
                            :
                                <Button 
                                    className={"profile-btn"}
                                    text={"Mute"}
                                    handleEvent={() => mute()}
                                />
                        }
                    </div>
                </div>
            

                <div className="profile-bio-container">
                    <p className="profile-bio">
                        {userBio?.bio}
                    </p>
                </div>

            {/* </div> */}
        </article>
    </section>
  )
}

export default ProfileCard
