import React, { useEffect } from 'react'
import "./comment-card.css"
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { getRepliesByCommentId } from '../../../services/comment/actions/crud/getRepliesByCommentId';
import { getCurrentUser } from '../../../libs/getCurrentUser';
import UserOption from '../../../components/userOptionModal/UserOption';
import useToggle from '../../../hooks/useToggle';
import CommentUpvote from './components/commentInteractions/commentUpvote/CommentUpvote';
import CommentDownvote from './components/commentInteractions/commentDownvote/CommentDownvote';
import CommentShare from './components/commentInteractions/commentShare/CommentShare';
import CommentReplies from './components/commentInteractions/commentReplies/CommentReplies';
import options from "./options.svg"
import profileImg from "./profileImg.svg"
import { getUserBio } from '../../../services/bio/actions/getUserBio';
import { selectBio } from '../../../services/bio/reducers/bioSlice';

const CommentCard = ({comments}) => {
    const [showUserOptionsMenu, setShowUserOptionsMenu] = useToggle(false)
    const navigate = useNavigate()
    const location = useLocation()
    const dispatch = useDispatch()
    const {commentId} = useParams()
    const currentUser = getCurrentUser()
    const currentUserBio = useSelector(selectBio)

    const {comment, replies, user, _id, post} = comments
    console.log(replies)

    const getRepliesByComment = () => {
        try{
          const payload = {
            options: {
              method: "GET",
              headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${currentUser.userToken}`
              }
            },
            commentId: commentId
          }
  
          dispatch(getRepliesByCommentId(payload))
        }
        catch(error){
          console.log(error)
        }
    }

    // useEffect(() => {
    //     getRepliesByComment()
    // }, [])

    const navigateToUserProfile = () => {
        if(user._id === currentUser.userUid && location.pathname.includes("/profile")){
            console.log("same user")
            return;
        }
       navigate(`/profile/${user.username}`, {state: user._id})
    }

    const userBio = currentUserBio?.find(bio => replies?.some(reply => reply?.user._id === bio?.user._id));
    console.log(userBio)
    
    const getCurrentUserAvatar = (userId) => {
        const userBio = currentUserBio?.find(bio =>  bio?.user._id === userId);
        return userBio?.avatar
    }
    
  return (
    <article >
        {
            replies &&  
            <ul 
                className="reply-list"
            >
                {replies?.map(comment => {
                    return <li 
                        className="reply-list-item"
                        key={comment._id}
                        onClick={(e) => {
                            e.stopPropagation()
                            // getRepliesByComment(comment._id)
                            navigate(`/comment/${comment.user.username}/${comment._id}`)
                        }}
                    >
                        <div className="reply-wrapper">
                            <div className="reply-profile-container">
                                <a 
                                    className="reply-profile-avatar-container"
                                    onClick={navigateToUserProfile}
                                    onKeyDown={(e) => e.key === "Enter" && navigateToUserProfile()}
                                    aria-label={`View profile of ${comment.user.username}`}
                                >
                                    <img src={getCurrentUserAvatar(comment.user._id)} title="avatar" className="reply-profile-avatar" alt={`${comment.user.username}'s profile avatar`}/>
                                </a>
                                <div className="reply-line"></div>
                            </div>

                            <div className="mc">

                                <div className="reply-main-content">
                                    <div className="reply-head">
                                            <div className="reply-name-date-container">
                                                <h3 
                                                    className="reply-user-name"
                                                    onClick={navigateToUserProfile}
                                                    onKeyDown={(e) => e.key === "Enter" && navigateToUserProfile()} 
                                                    tabIndex="0"
                                                >
                                                    {comment.user.username}
                                                </h3>
                                                <small className="reply-time">08: 21 AM</small>
                                            </div>
                                        {
                                        <div
                                            className={`reply-menu-container`} 
                                            onClick={(e) => {
                                                e.stopPropagation()
                                                setShowUserOptionsMenu()
                                            }}
                                            onKeyDown={(e) => {
                                                if(e.key === "Enter"){
                                                    return e.key === "Enter" && setShowUserOptionsMenu()
                                                }
                                            
                                            }}
                                            tabIndex="0"
                                        >
                                            <img src={options} className="reply-menu" alt="Reply menu options icon" />
                                            {showUserOptionsMenu && <UserOption user={comment.user} postId={_id}/>}
                                        </div>}
                                    </div>

                                    <div className="reply-content-container">
                                        <p className="reply-content">
                                        {comment.comment}
                                        </p>
                                    </div>
                                </div>

                                <div className="reply-interactions-container">
                                    <div className="reply-btn-wrapper">
                                        <CommentUpvote comments={comments} />
                                        <CommentDownvote comments={comments} />
                                        <CommentShare comments={comments}/>
                                        <CommentReplies comments={comments}/>
                                    </div>
                                </div>

                            </div>
                            
                        </div>
                    </li>
                })}
            </ul>
        }


    </article>
  )
}

export default CommentCard
