import React, { useEffect, useState } from 'react'
import "./post-page.css"
import { useParams } from 'react-router-dom'
import UserPostCard from '../../components/postCards/UserPostCard'
import { useDispatch, useSelector } from 'react-redux'
import { selectPostById } from '../../services/post/reducers/postSlice'
import TopLevelComment from '../../components/postCards/components/postInteraction/comment/components/topLevelComment/TopLevelComment'
import { getCurrentUser } from '../../libs/getCurrentUser'
import { getAllCommentsByPostId } from '../../services/comment/actions/crud/getAllCommentsByPostId'
import { API_URL } from '../../services/api'
import { selectAllComments } from '../../services/comment/reducers/commentSlice'
import { selectAllCommentPost } from '../../services/comment/reducers/commentPostSlice'
import EmptyQueryResult from '../query/components/renderQueryPage/components/emptyQueryResult/EmptyQueryResult'
import { useWindowWidth } from '../authentication'
import PageHeader from '../../components/pageHeader/PageHeader'

const PostPage = () => {
  const {postId} = useParams()
  const post = useSelector(state => selectPostById(state, postId))
  const dispatch = useDispatch()
  const currentUser = getCurrentUser()
  const {screenWidth} = useWindowWidth()

  const isMobile = screenWidth <= 767

  const getPostComments = () => {
    try{
      const payload = {
        options: {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${currentUser.userToken}`
          }
        },
        postId
      }

      dispatch(getAllCommentsByPostId(payload))
    }
    catch(error){
      console.log(error)
    }

  }

  useEffect(() => {
    getPostComments()
  }, [postId])

  if(!post){
    return <>
      {isMobile && <PageHeader />}
      <EmptyQueryResult emptySearchMessage={"Error! Couldn't find the page you're looking for."} />
    </>
  }

    const getRepliesByCommentId = () => {
      try{
        const payload = {
          options: {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${currentUser.userToken}`
            }
          },
          postId
        }

        dispatch(getAllCommentsByPostId(payload))
      }
      catch(error){
        console.log(error)
      }
    }

  return (
    <section className="post-page-section">
      {isMobile && <PageHeader />}
      <UserPostCard posts={post} />
    </section>
  )
}

export default PostPage
