import { createSlice, createEntityAdapter } from "@reduxjs/toolkit";
import { getAllCommentsByPostId } from "../actions/crud/getAllCommentsByPostId";

const commentPostAdapter = createEntityAdapter({
  // selectId: (entity) => entity._id,
});

const initialState = commentPostAdapter.getInitialState({
  status: "idle",
  error: null,
});

const commentPostSlice = createSlice({
  name: "commentPost",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getAllCommentsByPostId.pending, (state, action) => {
        state.status = "loading";
      })
      // .addCase(getAllCommentsByPostId.fulfilled, (state, action) => {
      //   state.status = "fulfilled";
      //   console.log(action.payload);
      //   commentPostAdapter.setAll(state, action.payload);
      // })
      .addCase(getAllCommentsByPostId.rejected, (state, action) => {
        state.error = action.error;
        console.log(action.error);
      });
  },
});

export const {
  selectAll: selectAllCommentPost,
  selectById: selectCommentPostById,
  selectIds: selectCommentPostIds,
} = commentPostAdapter.getSelectors((state) => state.commentPost);

export const getCommentPostStatus = (state) => state.commentPost.status;
export default commentPostSlice.reducer;
