import React, { useEffect, useState } from 'react'
import Form from '../../../../components/Form'
import "./login.css"
import { NavLink, useNavigate } from 'react-router-dom'
import brandLogo from '../../assets/svgs/brandLogo.svg'
import {otpImage,  useWindowWidth } from '../..'
import { useDispatch, useSelector } from 'react-redux'
import { loginAction } from '../../actions/authAction'
import { getLoginStatus } from '../../reducers/authSlice'
import Loader from '../../../../components/loader/Loader'
import loginImg from "./assets/images/login.png"

const LoginForm = () => {
  const {screenWidth} = useWindowWidth()
  // const [isLoggedIn, setIsLoggedIn] = useState(true)
  const loginStatus = useSelector(getLoginStatus)
  const [loginCredentials, setLoginCredentials]= useState({
    username: "",
    password: "",
    anonymous: ""

  })
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const template = {
    title: screenWidth < 618 && "Login",
    confirmedBtnText: "Sign in",
    fields :[
      {
        name: "username",
        value: loginCredentials.username,
        type: "text",
        placeholder: "Username"
      },
      {
        name: "password",
        value: loginCredentials.password,
        type: "password",
        placeholder: "Password"
      },
    ]
  }

  const handleFormChange = (e) => {
    try{
      const { name, value} = e.target;
      setLoginCredentials(oldData => {
        return {
          ...oldData,
          [name]: value
        }
      })

    }
    catch(error){
      // console.log(error)
    }
  }

  const handleUserLogin = async (e) => {
    e.preventDefault()
    try{
      const options = {
        method: "POST",
        body: JSON.stringify(loginCredentials),
      };
      dispatch(loginAction(options));
    
      setLoginCredentials({
        username: "",
        password: "",
        anonymous: ""
      })
    }
    catch(error){
      // console.log(error)
    }
  }

  useEffect(() => {
    if(loginStatus === true){
      navigate("/")

    }
  }, [loginStatus])

  if(loginStatus === "loading"){
    return <Loader text={"Logging in"} />
  }

  return (
    <article className="login-container">
        <div className="login-wrapper">
          {
            screenWidth >= 618 && <div className="login-image-container">
              <img src={otpImage} className="login-image" alt="Login avatar" />
              {screenWidth >= 618 && <h3>Welcome back</h3>}
              {screenWidth >= 618 && <p>...bridging the gap between social media and mental health.</p>}
            </div>
          }

          <div className="login-content-container">
            <div className="login-brand-container">
              <img src={brandLogo} className="login-brand-logo" alt="brand-logo" />
            </div>

            <Form 
              template={template} 
              onSubmit={handleUserLogin} 
              className={"login-form"} 
              onChange={handleFormChange}
              keyDownEvent={""}
            />

            <div className="new-user-call-to-action-container">
              <p className="call-to-action-text">Not a member?</p>
              <NavLink to="/register" className={"call-to-action-link"}>Sign up</NavLink>
            </div>
        </div>
      </div>
    </article>
  )
}

export default LoginForm


