import { createEntityAdapter, createSlice } from "@reduxjs/toolkit";
import { createComment } from "../actions/crud/createComment";
import { getAllCommentsByPostId } from "../actions/crud/getAllCommentsByPostId";
import { getRepliesByCommentId } from "../actions/crud/getRepliesByCommentId";
import { upvoteComment } from "../actions/interactions/upvoteComment";

const commentAdapter = createEntityAdapter({
  selectId: (entity) => entity._id,
});

const initialState = commentAdapter.getInitialState({
  status: "idle",
  error: null,
  postComments: [],
  replies: {},
});

const commentSlice = createSlice({
  name: "comment",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(createComment.pending, (state, action) => {
        state.status = "loading";
      })
      .addCase(createComment.fulfilled, (state, action) => {
        state.status = "fulfilled";
        console.log(action.payload, "creating comment");
        commentAdapter.addOne(state, action.payload);
      })
      .addCase(createComment.rejected, (state, action) => {
        console.log(action.error);
      })
      .addCase(getAllCommentsByPostId.pending, (state, action) => {
        commentAdapter.setAll(state, {});
      })
      .addCase(getAllCommentsByPostId.fulfilled, (state, action) => {
        console.log(action.payload, "post by id before");
        state.postComments = [];
        state.postComments = action.payload;
        // use selectid for this
        // if (action.payload === null) return;
        if (action.payload) {
          commentAdapter.addOne(state, action.payload);
        }
      })
      .addCase(getRepliesByCommentId.pending, (state, action) => {
        state.replies = {};
      })
      .addCase(getRepliesByCommentId.fulfilled, (state, action) => {
        // console.log(action.payload, "replies");
        state.replies = {};
        state.replies = action.payload;
      })
      .addCase(upvoteComment.fulfilled, (state, action) => {
        console.log(action.payload);
        // commentAdapter.updateOne(state, {
        //   id: action.payload.
        // })
      });
  },
});

export const {
  selectAll: selectAllComments,
  selectById: selectCommentById,
  selectIds: selectCommentIds,
} = commentAdapter.getSelectors((state) => state.comment);

export const getPostComment = (state) => state.comment.postComments;
export const getReplies = (state) => state.comment.replies;
export default commentSlice.reducer;
